import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Breadcrumb, Button, Row, Col } from 'antd';
import Styles from './breadCrumb.css';

class BreadCrumbCustom extends Component {
  state = {
  };
  static contextTypes = {
    router: PropTypes.object.isRequired
  };
  static propTypes = {
    info: PropTypes.object.isRequired,
    updateBread: PropTypes.func.isRequired
  };
  static defaultProps = {
    info: {curParent: {}, curChild: {}}
  };
  handleAddDecorator () {
    this.context.router.history.push('/add/decorator');
  }
  handleAddApi () {
    this.context.router.history.push('/add/api', {id: this.props.info.curParent.key || ''});
  }
  handleAddBread () {
    let key = this.context.router.route.location.pathname.split('/');
    return {
      'parent': key[1] === 'add' ? '' : '',
      'child': key[1] === 'add' && key[2] ? key[2] : ''
    };
  }
  handleBackDecorator () {
    this.props.updateBread();
  }
  render () {
    const { curParent, curChild } = this.props.info;
    const { parent, child } = this.handleAddBread();
    return (
      <Row type='flex' align='middle' justify='space-between' className={Styles.minHeight}>
        <Col>
          <Breadcrumb style={{ margin: '16px 0' }}>
            <Breadcrumb.Item>{parent || curParent.name || ''}</Breadcrumb.Item>
            <Breadcrumb.Item>{child || curChild.name || ''}</Breadcrumb.Item>
          </Breadcrumb>
        </Col>
        <Col>
          {
            curChild.key && <Button type='primary' className={Styles.marginLeft} icon='rollback' onClick={this.handleBackDecorator.bind(this)}></Button>
          }
          <Button type='primary' className={Styles.marginLeft} icon='plus' onClick={this.handleAddDecorator.bind(this)}></Button>
          {
            curParent.key && <Button type='primary' className={Styles.marginLeft} icon='plus' onClick={this.handleAddApi.bind(this)}></Button>
          }
        </Col>
      </Row>
    );
  }
}

export default BreadCrumbCustom;



