import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {Form, Input, Button, Checkbox, message as Message, Row, Col} from 'antd';
import Util from '@src/config/Util';
import Styles from './list.css';

import { changeMenu } from '../../store/menu/menu.action';
const FormItem = Form.Item;
const CheckboxGroup = Checkbox.Group;

const formItemLayout = {
  // labelCol: { span: 2 },
  // wrapperCol: { span: 12 }
};

const methods = ['get', 'post'];

class ListForm extends React.Component {
  state = {
    parentId: '',
    id: '',
    name: '',
    des: '',
    type: '',
    data: ''
  };
  componentWillMount () {
    const params = this.props.history.location.state;
    if (params) {
      Util.fetchData('/api/api/info', {
        data: {
          url: params.childId
        }
      }).then(res => {
        if (res.errorCode === 0) {
          res.data.length > 0 && this.setState({
            name: res.data[0].name,
            url: res.data[0].url,
            des: res.data[0].des,
            id: res.data[0].id,
            type: res.data[0].type,
            data: res.data[0].data
          });
        } else {
          Message.error(res.errorMessage || '');
        }
      });
    } else {
      window.location.pathname = '/';
    }
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        let data = {...values};
        data.parentId = this.props.history.location.state.id.slice(1);
        data.type = data.type.join(',');
        Util.fetchData('/api/add/api', {
          data
        }).then(res => {
          if (res.errorCode === 0) {
            Message.success('');
            this.props.updateMenu();
          } else {
            Message.error(res.errorMessage || '');
          }
        });
      }
    });
  }
  handleDelete = e => {
    e.preventDefault();
    Util.fetchData('/api/delete/api', {
      data: {url: this.state.url}
    }).then(res => {
      if (res.errorCode === 0) {
        Message.success('');
        this.props.updateMenu();
      } else {
        Message.error(res.errorMessage || '');
      }
    });
  }
  render () {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form>
        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('name', {
            initialValue: this.state.name,
            rules: [{
              required: true, message: '!'
            }]
          })(
            <Input size='large' placeholder=''/>
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('url', {
            initialValue: this.state.url,
            rules: [{
              required: true, message: '!'
            }, {
              pattern: /^\/\w+/, message: '/'
            }]
          })(
            <Input size='large' placeholder=''/>
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('des', {
            initialValue: this.state.des
          })(
            <Input size='large' placeholder=''/>
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('type', {
            initialValue: this.state.type === '' ? [] : this.state.type.split(','),
            rules: [{
              required: true, message: '!'
            }]
          })(
            <CheckboxGroup options={methods}/>
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label=""
        >
          {getFieldDecorator('data', {
            initialValue: this.state.data,
            rules: [{
              required: true, message: '!'
            }]
          })(
            <Input.TextArea placeholder='' rows={8}/>
          )}
        </FormItem>
        <FormItem>
          <Row type='flex'>
            <Col><Button className={Styles.button} size='large' type="primary" onClick={this.handleSubmit}></Button></Col>
            {
              this.state.id && <Col><Button size='large' type="primary" onClick={this.handleDelete}></Button></Col>
            }
          </Row>
        </FormItem>
      </Form>
    );
  };
}

ListForm.propTypes = {
  form: PropTypes.object.isRequired,
  updateMenu: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired
};

const List = Form.create()(ListForm);

const mapStateToProps = state => ({
  curMenu: state.menu.curMenu
});
const mapDispatchToProps = dispatch => ({
  updateMenu: () => dispatch(changeMenu())
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(List);



